$img_width: 100px;

.StoreAddItemPage {
  padding-top: 64px;
  padding-bottom: 64px;

  width: 100%;

  button:disabled {
    background-color: rgba(0,0,0,0) !important;
  }

  .page_title_text {
    font-size: 32px;
    font-weight: bold;
    color: #1a1a1a;
    text-align: center;
    margin-bottom: 64px;
  }

  .photo_upload_container {
    display: flex;
    align-items: center;
  }

  .box_label {
    font-size: 18px;
    font-weight: bold;
    color: #191919;
  }

  .box_container {
    margin-top: 40px;
  }

  .input_label {
    font-size: 12px;
    color: #000000;
    // margin-top: 20px;
  }

  .input_box{
    width: 100%;
    height: 52px;
    border-radius: 5px;
    border: solid 1px #e6e6e6;
    padding: 16px;
    font-size: 14px;
    color: #4d4d4d;
    margin-top: 8px;
  }

  .input_content_textarea {
    width: 100%;
    height: 400px;
    padding: 16px;
    border-radius: 5px;
    border: solid 1px #e6e6e6;
    margin-top: 6px;
    font-size: 14px;
  }

  .input_content_ask_textarea {
    width: 100%;
    height: 200px;
    padding: 16px;
    border-radius: 5px;
    border: solid 1px #e6e6e6;
    margin-top: 6px;
    font-size: 14px;
  }

  .input_content_ask_textarea::placeholder {
    color: #acacac;
  }

  .input_ask_textarea {
    width: 100%;
    height: 112px;
    padding: 16px;
    border-radius: 5px;
    border: solid 1px #e6e6e6;
    margin-top: 6px;
    font-size: 14px;
  }

  .input_length_text {
    font-size: 12px;
    text-align: right;
    color: #acacac;
  }

  .input_ask_explain{
    font-size: 12px;
    color: #acacac;
  }

  .select_box {
    width: 100%;
    height: 52px;
    border-radius: 5px;
    background-color: #f7f7f7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    color: #4d4d4d;
    padding-left: 16px;
    padding-right: 12px;
    position: relative;
    margin-top: 10px;
  }

  .select_tag {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
  }

  .button_container {
    display: flex;
    margin-top: 24px;
  }

  .button_back {
    width: 100%;
    height: 56px;
    border-radius: 4px;
    border: solid 1px rgba(0, 0, 0, 0.1);
    font-size: 18px;
    color: #191919;
    margin-right: 10px;
  }

  .button_ok {
    width: 100%;
    height: 56px;
    border-radius: 4px;
    background-color: #00bfff;
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
  }

  .camera_circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 43px;
    height: 43px;
    box-shadow: 7px 7px 25px 0 rgba(217, 217, 217, 0.4);
    background-color: #ffffff;
    border-radius: 50px;
  }

  .camera_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    border-radius: 20px;
    background-color: #f7f7f7;
  }

  .camera_img_container {
    width: $img_width;
    height: $img_width;
    margin-left: 8px;

    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 20px;
  }

  .camera_img {
    width: $img_width;
    // height: $img_width;
    // border-radius: 20px;
  }

  .fileUploader{
    display: none;
  }

  .limit_explain_text {
    margin-top: 5px;
    font-size: 12px;
    color: #acacac;
    // margin-left: 35px;
  }

  .ask_play_time_textarea {
    width: 100%;
    height: 78px;
    padding: 16px;
    border-radius: 5px;
    border: solid 1px #e6e6e6;
    margin-top: 6px;
    font-size: 14px;
  }

  .ask_play_time_textarea::placeholder {
    font-size: 14px;
    color: #acacac;
  }

  .input_container {
    display: flex;
    align-items: center;
    margin-top: 16px;
  }

  .necessary_dot_box {
    height: 56px;
  }
  .necessary_dot {
    width: 6px;
    height: 6px;
    background-color: #00bfff;
    border-radius: 50%;
    margin-left: 6px;
  }

  .necessary_box {
    width: 100%;
    height: 56px;
    border-radius: 8px;
    background-color: #f4f4f4;
    display: flex;
    align-items: center;
    padding-left: 16px;
    // margin-bottom: 24px;
  }

  .necessary_text {
    margin-left: 8px;
    font-size: 14px;
  }

  .input_box::placeholder {
    color: #acacac;
  }

  .input_content_textarea::placeholder {
    color: #acacac;
  }

  .type_contents_container {
    margin-top: 16px;
  }

  .type_contents_box {
    display: flex;
    align-items: center;
  }

  .type_contents_box_gap {
    margin-top: 16px;
  }

  .type_contents_label {
    font-size: 14px;
    margin-left: 4px;
    color: #191919;
  }

  .type_contents_explain_container {
    display: flex;
    margin-left: 8px;
    border-radius: 2px;
    background-color: #f4f4f4;
    padding: 1px 4px;
  }

  .type_contents_explain {
    font-size: 10px;
    font-weight: bold;
    color: #7f7f7f;
  }

  .type_contents_content_container {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .seller_answer_dom_textarea {
    width: 100%;
    height: 100px;

    padding: 16px;
    border-radius: 5px;
    border: solid 1px #e6e6e6;
    margin-top: 16px;
    font-size: 14px;
  }

  .seller_answer_dom_textarea::placeholder {
    color: #acacac;
  }

  .complited_type_agree_container {
    display: flex;
    text-align: left;
    margin-top: 40px;
  }
  
  .complited_type_agree_text {
    font-size: 14px;
    color: #191919;
    margin-left: 8px;
  }

  @media (max-width: 520px){
    /*ëª¨ë°ì¼ì¼ë*/
    padding-top: 32px;
    padding-bottom: 32px;
    padding-left: 10px;
    padding-right: 10px;

    .page_title_text {
      font-size: 22px;
      margin-bottom: 32px;
    }

    .type_contents_content_container {
      flex-direction: column;
      align-items: flex-start;
      margin-left: 4px;
    }

    .type_contents_label {
      margin-left: 0px;
    }

    .type_contents_explain_container {
      margin-left: 0px;
      margin-top: 5px;
    }
  }
}

#react_root {
  background-color: white;
}